<template>
  <v-container>
    <v-row dense>
      <v-toolbar flat color="white">
        <v-toolbar-title>Crud Atma Pedia</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">add item</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-col
        v-for="(item, i) in desserts"
        :key="i"
        cols=12
      >
        <v-card
          class="mx-auto"
          max-width="344"
          outlined
        >
          <v-list-item three-line>
            <v-list-item-content>
              <div class="overline mb-4">MAKANAN</div>
              <v-list-item-title class="headline mb-1" v-text="item.name" label="Dessert name" ></v-list-item-title>
              <v-row>
                <v-col>
                  <v-list-item-subtitle>Calories:</v-list-item-subtitle>
                </v-col>
                <v-col>
                  <v-list-item-subtitle v-text="item.calories" label="Calories"></v-list-item-subtitle>
                </v-col>
              </v-row>
              <v-spacer />
              <v-row>
                <v-col>
                  <v-list-item-subtitle>Fat:</v-list-item-subtitle>
                </v-col>
                <v-col>
                  <v-list-item-subtitle v-text="item.fat" label="Fat"></v-list-item-subtitle>
                </v-col>
              </v-row>
              <v-spacer />
              <v-row>
                <v-col>
                  <v-list-item-subtitle>Carbs:</v-list-item-subtitle>
                </v-col>
                <v-col>
                  <v-list-item-subtitle v-text="item.carbs" label="Carbs"></v-list-item-subtitle>
                </v-col>
              </v-row>
              <v-spacer />
              <v-row>
                <v-col>
                  <v-list-item-subtitle>Protein:</v-list-item-subtitle>
                </v-col>
                <v-col>
                  <v-list-item-subtitle v-text="item.protein" label="protein"></v-list-item-subtitle>
                </v-col>
              </v-row>
            </v-list-item-content>
          </v-list-item>

          <v-card-actions>
            <v-btn text @click="editItem(item)">Edit</v-btn>
            <v-btn text @click="deleteItem(item)">Delete</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>

<script>
import { dessertsRef } from '../firebase';

  export default {
    data: () => ({
      cek : -1,
      dialog: false,
      kosong: -1,
      checkDelete:[],
      search: '',
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: "Check", value:"multi", sortable: false},
        { text: 'Actions', value: 'action', sortable: false },
      ],

      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    firebase: {
      desserts : dessertsRef
    },

    computed: {
      formTitle () {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    methods: {
      close () {
        this.dialog = false
      },
      // methods untuk save
      save () {
        if (this.cek > -1) {
          dessertsRef.child(this.editedIndex).set({name: this.editedItem.name, calories: this.editedItem.calories, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        } else {
          dessertsRef.push({name: this.editedItem.name, calories: this.editedItem.calories, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        }
        this.close()
      },
      clear(){
        this.editedItem={}
        this.editedItem.name= ''
        this.editedItem.calories= 0
        this.editedItem.fat= 0
        this.editedItem.carbs= 0
        this.editedItem.protein= 0
        this.cek=-1
      },
      editItem(item){
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        confirm('Are you sure you want to delete this item?') && dessertsRef.child((item['.key'])).remove()
      },
      deleteItemBanyak(){
        console.log(this.checkDelete)
        for(this.kosong=0;this.kosong<this.checkDelete.length;this.kosong++){
            console.log(this.kosong);
            dessertsRef.child(this.checkDelete[this.kosong]).remove()
        }
        this.checkDelete=[]
        console.log(this.checkDelete)
      },
    },
  }
</script>
